<template>
  <div class="content">
    <div class="login">
      <p class="title">登录</p>
      <div class="login_content">
        <div class="username">
          <span class="txt">用户名</span>
          <input v-model="username1" type="text">
        </div>
        <div class="password">
          <span class="txt">密码</span>
          <input @keyup.enter = 'loginBtn' v-model="password1" type="password">
        </div>
      </div>
      <div @click="loginBtn" class="login_btn">登录</div>
    </div>
  </div>
</template>
<script>
  import {httpGet, setStore, getStore} from '../../utils/util'
  import {mapState, mapMutations} from 'vuex'
  export default {
    data() {
      return {
        username1: '',
        password1: ''
      }
    },
    computed: {
      ...mapState(["hasLogin", "userInfo"])
    },
    mounted () {
      this.getUserData();
    },
    methods: {
      ...mapMutations([
        'CHANGE_LOGIN',
        'INIT_USER',
        'EMPTY_USER'
      ]),
      loginBtn:function () {
        var that = this
        httpGet('/operators/login',{
          username: that.username1,
          password: that.password1
        },function (res) {
          if(res.data.error_code == 0) {
            setStore('userInfo',{ username: that.username1,
                                  password: that.password1,
                                  trader_id: res.data.operator.trader_id,
                                  sid:  res.data.operator.sid,
                                  role: res.data.operator.role,
                                  name: res.data.operator.trader_name
                                });
            that.INIT_USER();
            that.CHANGE_LOGIN(true);
            that.$router.push("/home");
          }else {
            alert(res.data.error_reason);
          }
        })
      },
      //判断本地是否存储有用户名密码
      getUserData:function () {
        var localuserInfo = getStore("userInfo");
        if (!localuserInfo) {
          return;
        }else  {
          localuserInfo = JSON.parse(localuserInfo);
          this.username1 = localuserInfo.username;
          this.password1 = localuserInfo.password;
        }
      }
    }
  }
</script>
<style scoped>
  .content{
    width:100%;
    min-height: 700px;
    height: 100%;
    padding-top: 100px;
    background: url("../../../static/bg.png") no-repeat;
  }
  .login{
    width: 425px;
    height: 225px;
    padding: 10px;
    background-color:rgba(48,148,250,0.2);
    margin: 0 auto;
    color: #fff;
  }
  .login .title{
    text-align: center;
    font-size: 24px;
  }
  .login .login_content{
    /*background-color: gainsboro;*/
    /*padding-right:50px;*/
    margin-bottom: 20px;
  }
  .login_content .username,
  .login_content .password{
    /*width: 385px;*/
    height: 50px;
    margin: 10px;
    padding-right: 40px;
    display: flex;
    /*justify-content:space-between;*/
  }
  .login_content .username .txt,
  .login_content .password .txt{
    width: 50px;
    line-height: 60px;
    margin-right: 20px;
    text-align: right;
    /*background-color: blue;*/
  }
  .login_content .username input,
  .login_content .password input{
    /*width: 305px;*/
    flex: 1;
    /*background-color: #fff;*/
  }
  .login .login_btn{
    width: 325px;
    height: 35px;
    margin: 0 auto;
    line-height: 35px;
    background-color: green;
    text-align: center;
    cursor: pointer;
  }
</style>



